import React, { Component } from "react";

const keyboard = () => (WrappedComponent: React.ComponentClass | React.FC) =>
  class HOC extends Component {
    focusinHandler: () => any;
    focusoutHandler: () => any;
    resizeHandler: () => any;
    state = {
      focus: false,
    };
    constructor(props: any) {
      super(props);
      this.focusinHandler = () => {};
      this.focusoutHandler = () => {};
      this.resizeHandler = () => {};
    }
    componentDidMount() {
      const ua = window.navigator.userAgent.toLocaleLowerCase();
      const isIOS = /iphone|ipad|ipod/.test(ua);
      const isAndroid = /android/.test(ua);
      if (isIOS) {
        //  IOS 
        let isReset = true; //

        this.focusinHandler = () => {
          isReset = false; //
          this.setState({
            focus: true,
          });
        };

        this.focusoutHandler = () => {
          isReset = true;
          setTimeout(() => {
            //
            if (isReset) {
              window.scroll(0, 0); //
            }
            this.setState({
              focus: false,
            });
          }, 30);
        };

        document.body.addEventListener("focusin", this.focusinHandler);
        document.body.addEventListener("focusout", this.focusoutHandler);
      }
      if (isAndroid) {
        //  Android 
        const originHeight = document.documentElement.clientHeight || document.body.clientHeight;

        this.resizeHandler = () => {
          const resizeHeight = document.documentElement.clientHeight || document.body.clientHeight;
          const activeElement = document.activeElement;
          if (resizeHeight < originHeight) {
            // 

            if (activeElement && (activeElement.tagName === "INPUT" || activeElement.tagName === "TEXTAREA")) {
              this.setState({
                focus: true,
              });
              setTimeout(() => {
                activeElement.scrollIntoView({ block: "center" }); //
              }, 0);
            }
          } else {
            // 
            this.setState({
              focus: false,
            });
          }
        };

        window.addEventListener("resize", this.resizeHandler);
      }
    }

    componentWillUnmount() {
      if (this.focusinHandler && this.focusoutHandler) {
        document.body.removeEventListener("focusin", this.focusinHandler);
        document.body.removeEventListener("focusout", this.focusoutHandler);
      }
      if (this.resizeHandler) {
        document.body.removeEventListener("resize", this.resizeHandler);
      }
    }

    render() {
      return <WrappedComponent {...{ ...this.props, focus: this.state.focus }} />;
    }
  };

export default keyboard;
